const languages = require('../data/global/languages');
const _ = require('lodash');

export default class QueryHandler {
    static queryMenu(list, lang) {
        let node = {data: {nav: []}};
        lang = languages.mappings[lang];
        list.allPrismicMenu.edges.map(( edge ) => {
           if(edge.node.lang === lang)  {
               node = edge.node;
           }
        });
        return node;
    }
    /**
     * Make menu tree from API menu results
     * @param {*} list 
     * @param {*} all 
     */
    static menu_tree(list, all = true) {
        var map = {}, roots = [], i;
        for (i = 0; i < list.length; i += 1) {
            map['menu_link_content:' + list[i].node.id] = i; // initialize the map
            list[i].children = []; // initialize the children
        }
        i = list.length;
        while (i--) {
            let element = Object.assign({}, list[i].node);
            list[i].weight = list[i].node.attributes.weight;
            element.weight = element.attributes.weight;
            if (element.attributes.parent !== null && list[map[element.attributes.parent]]) {
                // if you have dangling branches check that map[node.parentId] exists
                list[map[element.attributes.parent]].children.push(element);
                list.splice(i, 1); 
            } else {
                roots.push(element);
            }
        }
        list = _.orderBy(list, ['weight'], ['asc']);
        _.forEach(list, item => {
            if(item.children.length > 0) {
                item.children = _.orderBy(item.children, ['weight'], ['asc']);
            }
        });
        return !all ? roots : list;
    }
}