import React, { Component } from 'react';
import PropTypes from 'prop-types';
import logo from '../../images/logo.png';
import { graphql, StaticQuery } from 'gatsby';
import { Collapse, Navbar, NavbarToggler, NavbarBrand, Nav, NavItem, UncontrolledDropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap';
import StringHandler from '../../utils/string-handler';
import QueryHandler from '../../utils/query-handler';
import LanguageSwitcher from './languageSwitcher';

const languages = require('../../data/global/languages');

export default class MainHeader extends Component {

  static propTypes = {
    context: PropTypes.object.isRequired
  };

  static langs = [];

  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.state = {
      isOpen: false
    };
    this.langs = StringHandler.inverseObject(languages.mappings);
  }
  toggle() {
    this.setState({
      isOpen: !this.state.isOpen
    });
  }

  render() {
    let graphql_menu = languages.informations[this.props.context.lang].graphql_menu;
    return (
      <StaticQuery
        query={graphql`
        {
          allMenuEnJson {edges {node {id,attributes {weight, enabled, title, id,parent, link {uri,title}}}}},
          allMenuFrJson {edges {node {id,attributes {weight, enabled, title, id,parent, link {uri,title}}}}}
        }
        `
      }
        render={data  => (
          //data = QueryHandler.menu_tree(data[graphql_menu].edges),
              console.log(data),
          <div>
            <Navbar color="dark" light expand="lg">
              <NavbarBrand href="/" className="logo-container">
                  <img src={logo} alt="Parrot" className="logo-picture" />
              </NavbarBrand>
              <NavbarToggler onClick={this.toggle} />
              <Collapse isOpen={this.state.isOpen} navbar>
                <Nav className="ml-auto" navbar>
                  {QueryHandler.menu_tree(data[graphql_menu].edges).map(( nav ) => (
                    nav.children.length === 0 ? (
                    <NavItem key={nav.node.id}>
                      {StringHandler.urlMenuString(this.props.context.lang, nav.node.attributes, 'nav-link')}
                    </NavItem>
                    ) : (
                      <UncontrolledDropdown nav inNavbar key={nav.node.id}>
                        <DropdownToggle nav caret>
                          {nav.node.attributes.title}
                        </DropdownToggle>
                        <DropdownMenu right>
                        {nav.children.map(( item ) => (
                          <DropdownItem key={item.id}>
                          {StringHandler.urlMenuString(this.props.context.lang,item.attributes, 'dropdown-item')}
                          </DropdownItem>
                        )) }
                        </DropdownMenu>
                      </UncontrolledDropdown>
                    )
                  ))}
                  <LanguageSwitcher context={this.props.context} />
                </Nav>
              </Collapse>
            </Navbar>
          </div>
        )}
      />
    )
  }
}