import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Link } from 'gatsby';
import { UncontrolledDropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap';
import StringHandler from '../../utils/string-handler';
import { FormattedMessage } from 'react-intl'

import fr from '../../images/flags/fr.svg';
import en from '../../images/flags/en.svg';

const languages = require('../../data/global/languages');
const langs_flags = {fr: fr, en: en};

export default class LanguageSwitcher extends Component {

  static propTypes = {
    context: PropTypes.object.isRequired
  };

  mapObject(object, callback) {
    let lang = this.props.context.lang;
    return Object.keys(object).map(function (key) {
        if(key !== lang) {
            return callback(key, object[key]);
        }
    });
  }

  render() {
    let context = this.props.context;
    return (
        <div>
            <UncontrolledDropdown nav inNavbar>
            <DropdownToggle nav caret>
            <span className="language-name">
                <LanguageItem lang={this.props.context.lang} />
            </span>
            </DropdownToggle>
            <DropdownMenu right>
                {this.mapObject(languages.informations, function (key, value) {
                    return (
                    <DropdownItem key={key}>
                        <Link className="language-name" to={StringHandler.redirectI18n(context, key)}>
                            <LanguageItem lang={key} />
                        </Link>
                    </DropdownItem>)
                })}
            </DropdownMenu>
            </UncontrolledDropdown>
        </div>
    )
  }
}

const LanguageItem = ({ lang }) => {
    return (
        <span>
            <img src={langs_flags[lang]} className="flag-header" alt={lang} />
            <FormattedMessage id={languages.informations[lang].name} defaultMessage={languages.informations[lang].name} />
        </span>
    );
}